@import "../../../assets/styles/__mixins.sass"
@import "../../../assets/styles/__variables.sass"
@import "../../../assets/styles/__tegs.sass"

@import "../../../components/buttons/buttons.sass"
@import "../../../components/plugins/plugins.sass"
@import "../../../components/bullet-list/bullet-list.sass"
@import "../../../components/comment/comment.sass"
@import "../../../components/feedback/feedback.sass"
@import "../../../components/inputs/regular-input/regular-input.sass"
// стили к инпутам
.elements
  background: #ffffff url('../../../assets/img/uikit/group.svg') no-repeat top left

.elements, .elements__filter, .elements__comments
  background-color: $white
  @include flex-between()
  @include container()
  padding-top: 8.9375em

  &__filter
    padding-top: 7.9375rem

  &__description
    @include flex-between()
    font-size: $font-small

  &__info
    font-size: $font-small
    margin-bottom: 0.4875em

  &__state
    margin-top: 0.01rem

    &:nth-child(2)
      letter-spacing: 0.033rem

  &__field
    @include input-style()
    margin-bottom: 1.4rem
    letter-spacing: 0.055rem
    padding: 0.7rem
    width: 21.4rem

    &-wrap
      position: relative

      &-datepicker
        position: relative

        &::after
          @include arrow()
          top: 1.2em
          right: 1.1em

        &_short
          position: relative
          margin-bottom: 3em

          &::after
            @include arrow($right: 4.9em)
            top: 1.3em
            
    &-date
      @include input-style()
      width: 11.275em
      border: 0.0625em solid rgba(31, 32, 65, 0.25)
      border-radius: 0.31em
      padding: 0.9125em 0 0.9125em 0.9375em
      letter-spacing: 0.1em

    &-filter
      letter-spacing: 0.04em

    &-subscription
      letter-spacing: 0.12em

    &-filter, &-subscription
      @include input-style()
      padding: 1rem 2.2rem 1rem 0.9rem
      width: 18rem

.input-arrow
  position: absolute
  top: 0.65rem
  right: 3.5rem
  cursor: pointer
  background-color: transparent
  border: none 

.material-symbols-outlined
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)
  -webkit-background-clip: text
  -webkit-text-fill-color: transparent
  background-clip: text
  text-fill-color: transparent
  font-weight: 700

.filter
  &__description
    display: flex
    justify-content: space-between
    margin-bottom: .3125rem
  
  &__info, &__state
    font-size: .75rem

.date-dropdown
  @include flex-between()
  margin-top: 2.5rem
  margin-bottom: 2.5rem

// стили для кнопок

// эллипсовые кнопки
.buttons
  margin-bottom: 2.5em

  &__name
    font-size: $font-small
    margin-bottom: 1.5em

.ellipse-buttons
  &__wrap
    display: flex
    margin-bottom: 1.8125rem

.other-buttons
  padding-right: 0.6rem

// пагинация
.pagination__name
  font-size: $font-small
  margin-bottom: 1.2em

// dropdown с гостями
.guests
  margin-left: 6.5rem

  &__info
    margin-bottom: 0.4125em
    font-size: $font-small

  &__field, .lists__field
    width: 20em
    padding: 0.75em 0 0.5em 0.9375em
    border-radius: 0.325em
    border: 1px solid $light-blue
    margin-bottom: 6.125em

  &__title
    font-size: $font-small
    margin-bottom: 1.4em

  &__checkbox
    display: flex
    margin-bottom: 0.6875em

  &__option-title
    color: $dark-blue
    font-weight: 700
    margin-bottom: 0.3125em

  &__option-text
    color: $medium-blue
    max-width: 17rem

// dropdown и списки
.lists
  &__info
    font-size: $font-small
    margin-bottom: .3125rem